<template>
  <div>
    <el-row :gutter="24">
      <div class="panel m-l-md">
        <panel-title :title="$lang.neo4j.title">
        </panel-title>
        <div
            class="panel-body"
        >
          <el-row :gutter="24">
            <el-col :span="18">
              <el-form ref="form" :model="create_form" label-width="150px">
                <el-form-item :label="$lang.neo4j.createN" prop="text" style="display: inline">
                  <el-input
                      class="neo4j_input"
                      v-model="create_form.disease"
                      :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.disease"
                      size="small"
                      style="width:300px"
                  ></el-input>
                  <el-input
                      class="neo4j_input"
                      v-model="create_form.node_label"
                      :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.node_label"
                      size="small"
                      style="width:300px"
                  ></el-input>
                  <el-input
                      class="neo4j_input"
                      v-model="create_form.node_name"
                      :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.node_name"
                      size="small"
                      style="width:300px"
                  ></el-input>
                  <el-button
                      class="neo4j_button"
                      type="warning"
                      size="mini"
                      @click="onSubmitCreateForm"
                      :loading="onSubmitLoading"
                  >
                    <i class="fa fa-plus"></i>
                    {{ $lang.neo4j.create }}
                  </el-button>
                </el-form-item>
              </el-form>

              <el-form ref="form" :model="modify_form" label-width="150px">
                <el-form-item :label="$lang.neo4j.modifyN" prop="text" style="display: inline">
                  <el-input
                      class="neo4j_input"
                      v-model="modify_form.node_label"
                      :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.node_label"
                      size="small"
                      style="width:300px"
                  ></el-input>
                  <el-input
                      class="neo4j_input"
                      v-model="modify_form.node_name"
                      :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.node_name"
                      size="small"
                      style="width:300px"
                  ></el-input>
                  <el-input
                      class="neo4j_input"
                      v-model="modify_form.after_name"
                      :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.after_name"
                      size="small"
                      style="width:300px"
                  ></el-input>
                  <el-button
                      class="neo4j_button"
                      type="info"
                      size="mini"
                      @click="onSubmitModifyForm"
                      :loading="onSubmitLoading"
                  >
                    <i class="fa fa-wrench"></i>
                    {{ $lang.neo4j.modify }}
                  </el-button>
                </el-form-item>
              </el-form>

              <el-form ref="form" :model="delete_form" label-width="150px">
                <el-form-item :label="$lang.neo4j.deleteN" prop="text" style="display: inline">
                  <el-input
                      class="neo4j_input"
                      v-model="delete_form.node_label"
                      :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.node_label"
                      size="small"
                      style="width:300px"
                  ></el-input>
                  <el-input
                      class="neo4j_input"
                      v-model="delete_form.node_name"
                      :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.node_name"
                      size="small"
                      style="width:300px"
                  ></el-input>
                  <el-button
                      class="neo4j_button"
                      type="danger"
                      size="mini"
                      @click="onSubmitDeleteForm"
                      :loading="onSubmitLoading"
                  >
                    <i class="fa fa-remove"></i>
                    {{ $lang.neo4j.delete }}
                  </el-button>
                </el-form-item>
              </el-form>

              <el-form ref="form" :model="find_form" label-width="150px">
                <el-form-item :label="$lang.neo4j.findN" prop="text" style="display: inline">
                  <el-input
                      class="neo4j_input"
                      v-model="find_form.disease"
                      :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.disease"
                      size="small"
                      style="width:300px"
                  ></el-input>
                  <el-button
                      class="neo4j_button"
                      type="primary"
                      size="mini"
                      @click="onSubmitFindForm"
                      :loading="onSubmitLoading"
                  >
                    <i class="fa fa-search"></i>
                    {{ $lang.neo4j.find }}
                  </el-button>
                </el-form-item>
              </el-form>

              <el-form label-width="150px">
                <el-form-item :label="$lang.columns.output" prop="output">
                  <div id="canvas"
                       style="border:1px solid #e4e4e4;
                       width: 600px;height: 200px;border-radius: 0.5rem;">
                    <div v-text="info" v-if="showinfo" style="margin-left: 10px">
                    </div>
                  </div>
                </el-form-item>
              </el-form>

            </el-col>
          </el-row>
        </div>
      </div>
    </el-row>
  </div>
</template>

<style>
.neo4j_button {
  width: 120px;
  float: right;
  margin-top: 10px !important;

}
</style>

<script>
import PanelTitle from "../../components/PanelTitle";
import axios from 'axios';
import {ip, port} from "../../utils/regex";

export default {
  data() {
    return {
      create_form: {
        disease: "",
        node_label: "",
        node_name: "",
      },
      modify_form: {
        node_label: "",
        node_name: "",
        after_name: "",
      },
      delete_form: {
        node_label: "",
        node_name: "",
      },
      find_form: {
        disease: "",
      },
      info: {},
      showinfo:false,
      loadData: false,
      onSubmitLoading: false,
    };
  },
  methods: {
    onSubmitCreateForm() {
      this.onSubmitLoading = true;
      this.$http
          .post(this.$store.state.url.neo4j.create, this.create_form)
          .then(({data: info}) => {
            this.info = info;
            console.log(info);
            this.loading = false;
            this.showinfo = true;
          })
          .catch(() => {
            this.onSubmitLoading = false;
          });
    },
    onSubmitModifyForm() {
      this.onSubmitLoading = true;
      this.$http
          .post(this.$store.state.url.neo4j.modify, this.modify_form)
          .then(({data: info}) => {
            this.info = info;
            console.log(info);
            this.loading = false;
            this.showinfo = true;
          })
          .catch(() => {
            this.onSubmitLoading = false;
          });
    },
    onSubmitDeleteForm() {
      this.onSubmitLoading = true;
      this.$http
          .post(this.$store.state.url.neo4j.delete, this.delete_form)
          .then(({data: info}) => {
            this.info = info;
            console.log(info);
            this.loading = false;
            this.showinfo = true;
          })
          .catch(() => {
            this.onSubmitLoading = false;
          });
    },
    onSubmitFindForm() {
      this.onSubmitLoading = true;
      this.$http
          .post(this.$store.state.url.neo4j.find, this.find_form)
          .then(({data: info}) => {
            this.info = info;
            console.log(info);
            this.loading = false;
            this.showinfo = true;
          })
          .catch(() => {
            this.onSubmitLoading = false;
          });
    },
  },
  components: {
    PanelTitle,
  },
};
</script>
